import Index from "./index-model.js";
const index = new Index();
export default {
	data() {
		return {
			options: {},
			title: 'Hello',
			imageUrl: '',
			authorizationButton: null,
			userInfo: {},
			userInfoAll: {},
			// 轮播图相关
			bannerData: [],
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			// 客户经理数据对象
			managerNode: {}
		}
	},
	onLoad(options) {
		const that = this
		that.options = options
		that._onLoad()
	},
	onShow() {
		// 获取已授权类别
		const that = this
		uni.getSetting({
			success(res) {
				if (res.authSetting['scope.userInfo']) {
					// 隐藏授权按钮
					that.authorizationButton = false;
					that.$store.commit('updateAuthorizationButtonData', false);
				}
			},
			fail() {
				console.log("获取授权信息授权失败")
			}
		})
		// let token = index.get_storage('token_type', callBack);
		// if(token) {
		// 	that.getUserInfo(() => {
		// 		that.getRunData(() => {
		// 			callBack && callBack();
		// 		})
		// 	})
		// }
	},
	methods: {
		_onLoad(callBack) {
			const that = this
			that.imageUrl = index.base_image_url
			that.userInfo = that.$store.state.userInfo;
			// 轮播图加载
			this.getBanner(() => {
				callBack && callBack();
			})
			// 客户经理列表加载
			this.getManagerList(() => {
				callBack && callBack();
			})
			// that.wx_login(() => {
			// 	that.getUserInfo(() => {
			// 		if (that.userInfo.type == 'member') {
						
			// 		} else {
			// 			// 提示用户非会员
			// 			that.promptOpenVip()
			// 		}
			// 	})
			// })
		},
		// 进入--客户经理--详情页
		goManagerDetails(e) {
			const that = this;
			const id = index.get_data_set(e, "id");
			index.navigate_to(`/pages/managerDetails/managerDetails?id=${id}`);
		},
		// 进入--资讯--详情页
		goInformationDetails(e) {
			const that = this;
			const id = index.get_data_set(e, "id");
			index.navigate_to(`/pages/informationDetails/informationDetails?id=${id}`);
		},
		wx_login(callBack) {
			const that = this;
			that.authorizationButton = that.$store.state.authorizationButton;
			uni.login({
				provider: 'weixin',
				success: function(loginRes) {
					var code = loginRes.code;
					uni.getUserInfo({
						provider: 'weixin',
						success: function(infoRes) {
							that.userInfoAll = infoRes
							that.$store.commit('updateUserInfo', that.userInfo);
							that.$store.commit('updateAuthorizationButtonData', false);
							index.login({
								code: code,
								share_id: that.options.id,
								headimgurl: infoRes.userInfo.avatarUrl,
								nickname: infoRes.userInfo.nickName,
								sex: infoRes.userInfo.gender
							}, (res) => {
								if (res.status_code == 'ok') {
									index.set_storage('token', res.access_token);
									index.set_storage('token_type', res.token_type);
								}
								callBack && callBack();
							})
						}
					})
				}
			});
		},
		// 用户信息获取
		getUserInfo(callBack) {
			const that = this
			index.getUserInfo({}, (res) => {
				if (res.status_code == 'ok') {
					let userInfo = that.$store.state.userInfo;
					that.userInfo = Object.assign(userInfo, res.data)
					that.$store.commit('updateUserInfo', that.userInfo);
					that.$store.commit('updataSettingsInfo', res.settings);
				}
				callBack && callBack();
			})
		},
		// 加载轮播图
		getBanner(callBack) {
			const that = this
			index.getBanner({}, (res) => {
				if (res.code == '4000') {
					let list = res.data
					// let newList = []
					for (let i = 0; i < list.length; i++) {
						list[i].img = that.imageUrl + list[i].img
					}
					that.bannerData = list
					console.log(list)
				}
				callBack && callBack();
			})
		},
		// 客户经理列表
		getManagerList(callBack) {
			const that = this
			index.getManagerList({}, (res) => {
				if (res.code == '4000') {
					that.managerNode = res
					// console.log()
				}
				callBack && callBack();
			})
		},
		// 提示开通会员
		promptOpenVip(callBack) {
			uni.showModal({
				title: '系统提示',
				content: '系统检测到用户未开通会员，是否开通会员',
				success: res => {
					if (res.confirm) {
						index.navigate_to(`/pages/vip/vip`);
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		}
	},
	// 下拉刷新
	onPullDownRefresh() {
		var that = this;
		that.page = 1;
		that._onLoad(() => {
			uni.stopPullDownRefresh();
		});
	},
	//上拉加载更多
	// onReachBottom() {
	//   var that = this;
	//   if (that.last_page == that.page) {
	//     return;
	//   }
	//   that.page += 1;
	//   that.get_product_list();
	// },
	// 分享
	onShareAppMessage() {
		let shareData = {
			title: '',
			path: `pages/index/index?${this.userInfo.id}`,
			imageUrl: ''
		}
		return index.onShareAppMessage(shareData);
	}
}
